@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap");

:root {
  --primary-color: #f4020c; /* 브랜드 컬러 - 빨간색 */
  --secondary-color: #687683; /* 브랜드 컬러 - 그레이 */
  --black: #000000; /* 브랜드 컬러 - 검은색 */
  --white: #ffffff;
  --light-gray: #f5f5f5;
  --border-color: #e0e0e0;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  --error-color: #f44336;
  --inbound-color: #4caf50; /* 입고 색상 - 그린 */
  --outbound-color: #ff9800; /* 출고 색상 - 오렌지 */
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--light-gray);
  color: var(--black);
}

button {
  font-family: "Noto Sans KR", sans-serif;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 10px 20px;
  border-radius: 4px;
  font-weight: 500;
}

input,
select {
  font-family: "Noto Sans KR", sans-serif;
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  outline: none;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 16px;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.button-primary {
  background-color: var(--primary-color);
  color: var(--white);
}

.button-secondary {
  background-color: var(--secondary-color);
  color: var(--white);
}

.button-outlined {
  background-color: transparent;
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.toast {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  border-radius: 4px;
  z-index: 1000;
}
